import React from 'react';
import './index.scss';
import Slider from "react-slick";
import LazyLoad from 'react-lazyload';   // 懒加载
import { OverPack } from 'rc-scroll-anim';
import QueueAnim from 'rc-queue-anim';
import { Row, Col } from 'antd';
import img1 from './img/news1.jpg';
import img2 from './img/news2.jpg';
import img3 from './img/news3.jpg';
import img4 from './img/news4.jpg';
import img5 from './img/news5.jpg';
import img6 from './img/news6.jpg';

export default class News extends React.Component {
  state = {
    list: [
      { photo: img1, title: "海峡两岸DRG论坛--两岸交流献学术巨礼", time: "2019-08-06", msg: "2019 年 07 月 26 日，由上海市医院协会病案管理专业委员会、医院绩效管理专业委员会、医院医疗保险管理专业委员主办，上海交通大学医学院附属瑞金医院承办的海峡两岸疾病诊断相关分组（DRG）论坛在上海隆重举行，来自全国各地的相关DRG医疗专家学者，21个省市，36个城市的嘉宾共同参与了本次盛会。", url: 'https://mp.weixin.qq.com/s?__biz=MzI1MDQyNTI0OQ==&mid=2247483867&idx=1&sn=f4da2fa11ad1e9962157a26653defd9c&chksm=e9833b15def4b2036c3a5a3d2c6eca5b2e72763d59e6950f877ec7669ce6bab024a37ffced4b&mpshare=1&scene=1&srcid=&sharer_sharetime=1593485840294&sharer_shareid=f507b9d5e37b187893e4119fbee576c9&key=1990a9a74a8d8035fd54681dd88fe55af259d20c9eca969544ff8aef5a838510006bb651b06856764ac1a82b1d2a0272c622a4f6e241bda2adbbe10cdbebfd8c640f7b8956e2817a6c7e610cdf23c700&ascene=1&uin=MjgyMjMxNDYwMQ%3D%3D&devicetype=Windows+10+x64&version=62090070&lang=zh_CN&exportkey=AdMpo466gqhWQMadK3fSCx0%3D&pass_ticket=OKFsNu40cS9sTJY9YUECOXBCNFewFxcucmQC8QEpgttIfsIkca13m0pWGAeyyyB%2B' },
      { photo: img2, title: "忆长安，九月时——第28届全国病案学术会议议程安排", time: "2019-09-17", msg: "2019年9月17日(周二)至9月19日(周四)，第28届中国医院协会病案专业委员会学术会议在古城西安举办。本次大会将围绕公立医院绩效考核、DRGS付费制度改革、病案数据质量监控、电子病历时代病案的管理、AI技术与病案管理等重点议题进行深入交流，推动病案管理学科更好地助力医疗改革和发展，为建设健康中国做出伟大贡献。", url: 'https://mp.weixin.qq.com/s?__biz=MzI1MDQyNTI0OQ==&mid=2247483906&idx=1&sn=fc7a2c767e49432d372e3e6776c668a0&chksm=e98338ccdef4b1da1bd94513d3d282e80e00944e4f1052a139c23b0513b2e348ea3ca9b8d381&mpshare=1&scene=1&srcid=&sharer_sharetime=1593487490312&sharer_shareid=f507b9d5e37b187893e4119fbee576c9&key=b251233686787f70aff03e81bddd4ab5eb57b724aba829f6d41181c233925905536f6991a90c2bc11c56dbf091bc1be89fb22f18af73947e9f02cde7736ea4a56b8d4b60275a71f467144f18f620a17c&ascene=1&uin=MjgyMjMxNDYwMQ%3D%3D&devicetype=Windows+10+x64&version=62090070&lang=zh_CN&exportkey=ASeXVsrL1iZFWtiiOmtu%2B8E%3D&pass_ticket=OKFsNu40cS9sTJY9YUECOXBCNFewFxcucmQC8QEpgttIfsIkca13m0pWGAeyyyB%2B' },
      { photo: img3, title: "廿八病案，不负时代---第28届中国医院协会病案专业委员会学术会议圆满落幕", time: "2019-09-26", msg: "金秋九月，凉风送爽。在这历时两天的会议中，大会围绕当前病案人关心的热点问题，如HQMS数据上报问题、国家三级公立医院绩效考核、DRG在绩效评价和付费的应用、无纸化病历及电子病历流程再造、人工智能在病案管理中的应用、省级首页数据库的建设、病案数据在医院绩效评价的应用等方面的主题。", url: 'https://mp.weixin.qq.com/s?__biz=MzI1MDQyNTI0OQ==&mid=2247483920&idx=1&sn=df0c08fb0de3a2b632e55e001a69358a&chksm=e98338dedef4b1c80b09e4f3ad04bd5fa57788617e469e2dfac8a0bc106dfc522e4abb3b53aa&mpshare=1&scene=1&srcid=&sharer_sharetime=1593487500143&sharer_shareid=f507b9d5e37b187893e4119fbee576c9&key=1990a9a74a8d803542eb553f5612ffd104d2182fbb640970081644231f1b350996b972d0d1ef4421b690c9194821a55573bfffd8fd455ebd497d32dda244235dd9f3aa222d4dc70b9e438d8e4b34a8c3&ascene=1&uin=MjgyMjMxNDYwMQ%3D%3D&devicetype=Windows+10+x64&version=62090070&lang=zh_CN&exportkey=Aay1XKXCE29sgxOl3UQSQQw%3D&pass_ticket=OKFsNu40cS9sTJY9YUECOXBCNFewFxcucmQC8QEpgttIfsIkca13m0pWGAeyyyB%2B' },
      { photo: img4, title: "【喜讯】上海联众顺利通过CMMI L5认证,获得国际权威认可", time: "2019-10-23", msg: "百舸争流，激荡回声。不久前，上海联众顺利通过CMMI5级认证。这一奖项标志着上海联众在医疗信息系统软件开发行业成为了国内为数不多的CMMI5级企业，同时也标志着上海联众的软件开发能力、项目管理能力、过程组织能力以及方案支付能力都已达到了国际标准水平，成为了这一行业的排头兵。", url: 'https://mp.weixin.qq.com/s?__biz=MzI1MDQyNTI0OQ==&mid=2247483954&idx=1&sn=08ef57656bc125a1de59817332d8ed98&chksm=e98338fcdef4b1ea4ddb9e3e375f178eac41061aed408b0b4821083b008cc51295aa76796aa5&mpshare=1&scene=1&srcid=&sharer_sharetime=1593487507863&sharer_shareid=f507b9d5e37b187893e4119fbee576c9&key=6d309a22c00ffb6bb1993a5c1911e8c19cf195d3e9fddf1650678d91b29fcf280cde51983ef86d4ad26f6cc276ee5fa8968607881a742ae453032dd6e96ace1acb286747ee895266068379f9f23c4c75&ascene=1&uin=MjgyMjMxNDYwMQ%3D%3D&devicetype=Windows+10+x64&version=62090070&lang=zh_CN&exportkey=ATt4CVX9A6npVkA0bggv39I%3D&pass_ticket=OKFsNu40cS9sTJY9YUECOXBCNFewFxcucmQC8QEpgttIfsIkca13m0pWGAeyyyB%2B' },
      { photo: img5, title: "“豫”见DRG | 上海联众邀您共赴第四届DRG论坛", time: "2019-12-04", msg: "“当前世界正面临百年未有之大变局”。作为民生之本的医疗行业也正在面临变动。随着“逆全球化”的推波助澜，全球医疗费用不断上涨。如何控费和加强医院内部管理成为横亘于我们面前的难题。2017年起，国家选择以DRG为主的部分地区开展按疾病诊断相关分组（DRG）付费试点，鼓励各地完善按人头、按床日等多种付费方式。以DRG为主的这一支付方式的改革推动了医疗体系变革，为医疗信息化建设作出了贡献。", url: 'https://mp.weixin.qq.com/s?__biz=MzI1MDQyNTI0OQ==&mid=2247483970&idx=1&sn=de0a6f6ed70c8ffb0f1a10819b858467&chksm=e983388cdef4b19a9c79d0b41e0c9d502778aa3b37692e5250b7a06a6d3a343984a5d6d37226&mpshare=1&scene=1&srcid=&sharer_sharetime=1593487515542&sharer_shareid=f507b9d5e37b187893e4119fbee576c9&key=1990a9a74a8d803521d588699a06c8ab2000589e02f31c7a0c687e3881b2b2ad32760a5dc813a536c68b7f083b545328314421f6d1827fb9445ffb1b677dc4ad62f82e4049fddcdf39c9b1a97613634b&ascene=1&uin=MjgyMjMxNDYwMQ%3D%3D&devicetype=Windows+10+x64&version=62090070&lang=zh_CN&exportkey=AQqFKcXlx9iEy%2FQOSTl7laQ%3D&pass_ticket=OKFsNu40cS9sTJY9YUECOXBCNFewFxcucmQC8QEpgttIfsIkca13m0pWGAeyyyB%2B' },
      { photo: img6, title: "豫见未来｜第四届国家疾病诊断相关分组(DRG）论坛在豫成功举办", time: "2019-12-11", msg: "岁末天气寒冷，但位于历史名城郑州的黄河迎宾馆内气氛这几天格外热烈。为贯彻DRG“三医联动”，更广泛且有效地开展按疾病诊断相关分组(DRG)付费试点。由国家卫生健康委医院管理研究所和海峡两岸医药卫生协会主办，河南省人民医院承办的第四届国家疾病诊断相关分组(DRG）论坛，将重点讨论DRG对质量管控、支付方式、绩效考核等方面带来的重大变革，针对提升病案质量，改善医院模式，规范医疗行为，控制不合理的医疗费用，减轻群众就医负担等核心问题提出解决方案。", url: 'https://mp.weixin.qq.com/s?__biz=MzI1MDQyNTI0OQ==&mid=2247483977&idx=1&sn=fe3cb8d9eb794dc373e1eda9b9aa443a&chksm=e9833887def4b191fa5364800865555ccd1a182c54c0f7285de4bf6be1db9d963a2d44fee1c1&mpshare=1&scene=1&srcid=&sharer_sharetime=1593487524516&sharer_shareid=f507b9d5e37b187893e4119fbee576c9&key=7d7ed67221fd06ee81c2350898a5a468f0a55973dc43f05db43b501c468fc2e304a057b7b81de55837becbfa2f482292106a35fdf2464600b6238a3dbb2b25b3f3d723a9f82bdd48ca6ccfd017611801&ascene=1&uin=MjgyMjMxNDYwMQ%3D%3D&devicetype=Windows+10+x64&version=62090070&lang=zh_CN&exportkey=ARwDNHB2OoHUiCfMKQIc9DY%3D&pass_ticket=OKFsNu40cS9sTJY9YUECOXBCNFewFxcucmQC8QEpgttIfsIkca13m0pWGAeyyyB%2B' },
    ]
  }
  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      initialSlide: 0,
      autoplay: true,
      responsive: [
        {
          breakpoint: 1400,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 992,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            initialSlide: 2
          }
        },
        {
          breakpoint: 576,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return (
      <div id="news">
        <div className="news_shell">
          <Row>
            <Col xs={{span:20,offset:2}} sm={{span:20,offset:2}} lg={{span:20,offset:2}} xl={{span:18,offset:3}} xxl={{span:18,offset:3}}>
              <OverPack playScale={0.3} key="newtitle">
                <QueueAnim type='bottom' delay={100} duration={800} key="newsAnim1">
                  <h3 className="news_title" key="newstitle"><span>新闻动态</span></h3>
                </QueueAnim>
              </OverPack>
              <OverPack  playScale={0.3} key="newcontent">
                <QueueAnim type='bottom' delay={200} duration={800} key="newsAnim2">
                  <div className="news_pc" key="newscontent">
                    <Slider {...settings}>
                      {this.state.list.map((item, index) => {
                        return (
                          <a href={item.url} target="_blank" rel='noopener noreferrer' key={index} >
                            <div className="newsbox">
                              <LazyLoad ><img src={item.photo} alt="" /></LazyLoad>
                              <section className="news_inner">
                                <p className='news_h4'>{item.title}</p>
                                <p className="news_time">{item.time}</p>
                                <p className="news_words" style={{"WebkitBoxOrient": "vertical"}}>{item.msg}</p>
                              </section>
                            </div>
                          </a>
                        )
                      })}
                    </Slider>
                  </div>
                </QueueAnim>
              </OverPack>
            </Col>
          </Row>
        </div>
      </div>
    )
  }

}